        <swiper :options="swiperOption" class="swiper-box">
            <slot name="banner"></slot>
        </swiper>
        <div class="swiper-pagination white" slot="pagination"></div>
    </div>
</template>

<script>
    import 'swiper/dist/css/swiper.css'
